window.dom = {
    /**
     * ÂàõÂª∫ËäÇÁÇπ
     */
    create(string) {
        const container = document.createElement("template")
        container.innerHTML = string.trim()
        return container.content.firstChild
    },
    /**
     * Âú®‰∏Ä‰∏™ËäÇÁÇπÂêéÈù¢Êñ∞Â¢û‰∏Ä‰∏™ËäÇÁÇπ
     */
    after(node, newNode) {
        node.parentNode.insertBefore(newNode, node.nextSibling)
    },
    /**
     * Âú®‰∏Ä‰∏™ËäÇÁÇπÁöÑÂâçÈù¢ÊèíÂÖ•‰∏Ä‰∏™ËäÇÁÇπ
     */
    before(node, newNode) {
        node.parentNode.insertBefore(newNode, node)
    },
    /**
     * Âú®‰∏Ä‰∏™Áà∂ËäÇÁÇπÈáåÈù¢Êñ∞Â¢û‰∏Ä‰∏™Â≠êËäÇÁÇπ
     */
    append(parent, newNode) {
        parent.appendChild(newNode)
    },
    /**
     * Âú®‰∏Ä‰∏™ËäÇÁÇπÂ§ñÈù¢Êñ∞Â¢û‰∏Ä‰∏™Áà∂ËäÇÁÇπÔºå‰æãÂ¶Ç:divÂ§ñÈù¢ÂÜçÊ∑ªÂä†‰∏ÄÂ±Çdiv
     * ÂéüÁêÜ:
     * ÂÖàÊääparentÊîæÂà∞nodeÁöÑÂâçÈù¢
     * ÁÑ∂ÂêéÊäänodeÊîæÂà∞parentÁöÑÈáåÈù¢
     */
    wrap(node, parent) {
        dom.before(node, parent)
        dom.append(parent, node)
    },
    /**
     * Âà†Èô§ËäÇÁÇπ
     * ËøôÊòØÂÖºÂÆπIEÁöÑÂÜôÊ≥ï,ÈÄöËøáÁà∂ËäÇÁÇπÂà†Èô§Â≠êËäÇÁÇπ
     */
    remove(node) {
        node.parentNode.removeChild(node)
        return node
    },
    /**
     * Âà†Èô§Âêé‰ª£
     */
    empty(node) {
        const { childNodes } = node
        const array = []
            // for (let i = 0; i < childNodes.length - 1; i++) {
            //     dom.remove(childNodes[i])
            //     array.push(childNodes[i])
            // }
            // return array
        let x = node.firstChild
        while (x) {
            array.push(dom.remove(node.firstChild))
            x = node.firstChild
        }
        return array
    },
    /**
     * ‰øÆÊîπËäÇÁÇπ
     * Â¶ÇÊûúÂèÇÊï∞‰∏∫3‰∏™,ÂàÜÂà´ÊòØËäÇÁÇπÂêçÁß∞„ÄÅÂ±ûÊÄßÂêç„ÄÅÂ±ûÊÄßÂÄºÔºåÂàôÊòØ‰øÆÊîπËäÇÁÇπ
     * Â¶ÇÊûúÂèÇÊï∞‰∏∫2‰∏™,ÂàÜÂà´ÊòØËäÇÁÇπÂêçÁß∞„ÄÅÂ±ûÊÄßÂêçÔºåÂàôÊòØÊü•ËØ¢ËäÇÁÇπ
     */
    attr(node, name, value) { //ÈáçËΩΩ
        if (arguments.length === 3) {
            node.setAttribute(name, value)
        } else if (arguments.length === 2) {
            return node.getAttribute(name)
        }
    },
    /**
     * ËÆæÁΩÆËäÇÁÇπÁöÑÂÜÖÂÆπ
     */
    text(node, string) { //ÈÄÇÈÖç
        if (arguments.length === 2) {
            if ('innerText' in node) {
                node.innerText = string
            } else {
                node.textContent = string
            }
        } else if (arguments.length === 1) {
            if ('innerText' in node) {
                return node.innerText
            } else {
                return node.textContent
            }
        }
    },
    /**
     * ‰øÆÊîπËäÇÁÇπÂÜÖÂÆπ
     */
    html(node, string) {
        if (arguments.length === 2) {
            node.innerHTML = string
        } else if (arguments.length === 1) {
            return node.innerHTML
        }
    },
    /**
     * ‰øÆÊîπËäÇÁÇπÁöÑstyle
     */
    style(node, name, value) {
        if (arguments.length === 3) {
            //dom.style(argument1,argument2,argument3)
            node.style[name] = value
        } else if (arguments.length === 2) {
            //dom.style(argument1,argument2)
            if (typeof name === 'string') {
                return node.style[name]
            } else if (name instanceof Object) {
                //dom.style(argument1)
                const object = name
                for (let key in name) {
                    node.style[key] = object[key]
                }
            }
        }
    },
    /**
     * Âú®‰∏Ä‰∏™ËäÇÁÇπÈáåÊ∑ªÂä†‰∏Ä‰∏™class
     */
    class: {
        add(node, className) {
            node.classList.add(className)
        },
        /**
         * Âú®‰∏Ä‰∏™ËäÇÁÇπÈáåÂà†Èô§‰∏Ä‰∏™class
         */
        remove(node, className) {
            node.classList.remove(className)
        },
        /**
         * Êü•ËØ¢ËäÇÁÇπÈáåÊúâÊ≤°ÊúâËøô‰∏™class
         */
        has(node, className) {
            return node.classList.contains(className)
        }
    },
    /**
     * Ê∑ªÂä†onÁõëÂê¨
     */
    on(node, eventName, fn) {
        node.addEventListener(eventName, fn)
    },
    /**
     * ÁßªÈô§ÁõëÂê¨
     */
    off(node, eventName, fn) {
        node.removeEventListener(eventName, fn)
    },
    /**
     * Ê†πÊçÆÈÄâÊã©Âô®Êü•ÊâæËäÇÁÇπ
     */
    find(selector, scope) {
        return (scope || document).querySelectorAll(selector)
    },
    /**
     * Êü•ÊâæÁà∂ËäÇÁÇπ
     */
    parent(node) {
        return node.parentNode
    },
    /**
     * Êü•ÊâæÂ≠êËäÇÁÇπ
     */
    children(node) {
        return node.children
    },
    /**
     * Êü•ÊâæËØ•ËäÇÁÇπÁöÑÂÖÑÂºüÂßêÂ¶π
     */
    siblings(node) {
        return Array.from(node.parentNode.children).filter(n => n !== node)
    },
    /**
     * Êü•Êâæ‰∏ã‰∏Ä‰∏™ËäÇÁÇπ
     */
    next(node) { //3Ë°®Á§∫ÊñáÊú¨Ôºå1Ë°®Á§∫ËäÇÁÇπ
        let x = node.nextSibling
        while (x && x.nodeType === 3) {
            x = x.nextSibling
        }
        return x
    },
    /**
     * Êü•Êâæ‰∏ä‰∏Ä‰∏™ËäÇÁÇπ
     */
    previous(node) {
        let x = node.previousSibling
        while (x && x.nodeType === 3) {
            x = x.previousSibling
        }
        return x
    },
    /**
     * ÈÅçÂéÜËäÇÁÇπ
     */
    each(nodeList, fn) {
        for (let i = 0; i < nodeList.length; i++) {
            fn.call(null, nodeList[i])
        }
    },
    /**
     * Ëé∑ÂèñÂÖÉÁ¥†ÊéíË°åÁ¨¨Âá†
     */
    index(node) {
        const list = dom.children(node.parentNode)
        let i
        for (i = 0; i < list.length; i++) {
            if (list[i] === node) {
                break
            }
        }
        return i
    }
}